import React, { useEffect, useState } from "react";
import { FaWallet } from "react-icons/fa";

import Container from "../atoms/Container";
import Text from "../atoms/Text";
import SubText from "../atoms/SubText";

import WalletCard from "../molecules/WalletCard";
import WalletPreview from "../molecules/WalletPreview";
import Loader, { WalletPreviewLoader } from "../molecules/Loader";

import Upgrade from "../organisms/Upgrade";
import Pending from "../organisms/Pending";
import WalletChart from "../organisms/WalletChart";
import MyWallets from "../organisms/MyWallets";
import RecentTransactions from "../organisms/RecentTransactions";
import Referral from "../organisms/Referral";

import DashboardLayout from "../templates/Dashboard";

import { useProfile } from "../../hooks/useProfile";
import { useWallets } from "../../hooks/useWallets";
import { useBalance } from "../../hooks/useBalance";

const Dashboard = () => {
  const { profile } = useProfile();
  const { total, deposit, bonus, profit } = useBalance();
  const { wallets, loading: loadingWallets } = useWallets();

  const [selectedWallet, setSelectedWallet] = useState(null);

  useEffect(() => {
    if (wallets) setSelectedWallet(wallets[0]);
  }, [wallets]);

  return (
    <DashboardLayout>
      <Upgrade />

      {/* portfolio new start */}
      <Container p="12px" wide>
        <Text font="16px" p="12px 0" bold>
          Welcome {profile.firstName},
        </Text>

        <Container
          color="white"
          display="grid"
          gap="12px"
          media={{
            templatecolumns: "1fr 1fr"
          }}
          wide
        >
          <Container bg="board" radius="8px" p="12px" flexCol="flex-start" wide>
            <Text p="0" bold flexalign>
              <SubText font="18px" p="0" m="0 8px 0 0" opacity="0.6" flexalign>
                <FaWallet />
              </SubText>
              Total Balance
            </Text>
            <Text font="20px" p="0" m="8px 0 0 0" bold>
              {total}
              <SubText font="18px" p="0" m="0 0 0 4px">
                USD
              </SubText>
            </Text>
          </Container>

          <Container bg="board" radius="8px" p="12px" flexCol="flex-start" wide>
            <Text p="0" bold flexalign>
              <SubText font="20px" p="0" m="0 8px 0 0" opacity="0.6" flexalign>
                <FaWallet />
              </SubText>
              Profit
            </Text>
            <Text font="20px" p="0" m="8px 0 0 0" bold>
              {profit}
              <SubText font="18px" p="0" m="0 0 0 4px">
                USD
              </SubText>
            </Text>
          </Container>

          <Container bg="board" radius="8px" p="12px" flexCol="flex-start" wide>
            <Text p="0" bold flexalign>
              <SubText font="20px" p="0" m="0 8px 0 0" opacity="0.6" flexalign>
                <FaWallet />
              </SubText>
              Bonus
            </Text>
            <Text font="20px" p="0" m="8px 0 0 0" bold>
              {bonus}
              <SubText font="18px" p="0" m="0 0 0 4px">
                USD
              </SubText>
            </Text>
          </Container>

          <Container bg="board" radius="8px" p="12px" flexCol="flex-start" wide>
            <Text p="0" bold flexalign>
              <SubText font="20px" p="0" m="0 8px 0 0" opacity="0.6" flexalign>
                <FaWallet />
              </SubText>
              Deposit
            </Text>
            <Text font="20px" p="0" m="8px 0 0 0" bold>
              {deposit}
              <SubText font="18px" p="0" m="0 0 0 4px">
                USD
              </SubText>
            </Text>
          </Container>
        </Container>
      </Container>
      {/* portfolio new end */}

      <Pending />

      {/* wallet preview start */}
      {!selectedWallet ? (
        <WalletPreviewLoader />
      ) : (
        <Container p="12px 0" id="wallet-preview" wide>
          <WalletPreview wallet={selectedWallet} />
          <WalletChart
            wallet={selectedWallet}
            reverse
            h="300px"
            media={{
              breakpoint: "md",
              h: "364px",
            }}
          />
        </Container>
      )}
      {/* wallet preview end */}

      {/* top wallets start */}
      <Text font="16px" p="0 12px 12px" bold>
        Top Wallets
      </Text>
      <Container p="12px" display="grid" flow="column" gap="12px" scrollX wide>
        {loadingWallets ? (
          Array(3)
            .fill()
            .map((_, i) => <Loader key={i} h="200px" w="280px" radius="8px" />)
        ) : wallets.length ? (
          wallets.map((wallet) => (
            <WalletCard
              key={wallet._id}
              wallet={wallet}
              action={setSelectedWallet}
              to="wallet-preview"
              scrollto="true"
              smooth={true}
              duration={400}
              offset={-50}
            />
          ))
        ) : (
          <Container minH="200px" flex="center">
            <Text opacity="0.6" bold>
              No Wallets
            </Text>
          </Container>
        )}
      </Container>
      {/* top wallets end */}

      <RecentTransactions />
      <MyWallets />
      {process.env.REACT_APP_REFERRAL_BONUS && <Referral />}
    </DashboardLayout>
  );
};

export default Dashboard;
